import React, { Component } from 'react';
import './SidePanel.css'
import Station from './Station.js';
import Search from './Search.js';
import Box from '@material-ui/core/Box';
import Collapse from '@material-ui/core/Collapse';
import CloseIcon from '@material-ui/icons/Close';
import IconButton from '@material-ui/core/IconButton';
import { withRouter } from "react-router-dom";
import stationService from '../services/StationService';

class SidePanel extends Component {
    constructor(props) {
        super(props);
        this.handleCloseStation = this.handleCloseStation.bind(this);
        let searchParams = new URLSearchParams(props.location.search);
        let station = null;
        let searchActive = true;
        let destination = searchParams.get('destination');
        if (destination) {
            station = stationService.getStationBySlug(destination);
            if (station) {
                searchActive = false;
            } else {
              destination = null;
            }
        }
        this.state = {
            searchActive: searchActive,
            station: station,
            destination: destination,
        }
    }

    componentDidUpdate(prevProps) {
        let searchParams = new URLSearchParams(this.props.location.search);
        if (this.state.destination && !searchParams.get('destination')) { // no destination anymore
            this.setState({
                searchActive: true,
                station: null,
                destination: null,
            });
        } else if (
            (!this.state.destination && searchParams.get('destination')) || // first destination
            (this.state.destination !== searchParams.get('destination')) // new destination
        ) {
            let station = stationService.getStationBySlug(searchParams.get('destination'));
            this.setState({
                searchActive: false,
                station: station,
                destination: searchParams.get('destination'),
            });
        }
    }

    handleCloseStation() {
        this.props.history.push('');
        this.setState({
            searchActive: true
        });
    }

    render() {
        return (
            <Box pl={1} className="SidePanel">
                <img alt="logo" src={process.env.PUBLIC_URL + "/logo.png"} width="30%"/>
                <Collapse in={this.state.searchActive}>
                    <Search onSearchChange={this.props.onSearchChange}></Search>
                </Collapse>
                { !this.state.searchActive &&
                    <Box m={2}>
                        <Box display="flex" justifyContent="flex-end">
                            <IconButton onClick={this.handleCloseStation}><CloseIcon /></IconButton>
                        </Box>
                        <Station station={this.state.station} />
                    </Box>
                }
            </Box>
        )
    }
}

export default withRouter(SidePanel);
